// Haversine formula to calculate distance between two coordinates in kilometers
export function haversineDistance(lat1: number, lon1: number, lat2: number, lon2: number): number {
  const R = 6371 // Earth's radius in km
  const dLat = toRad(lat2 - lat1)
  const dLon = toRad(lon2 - lon1)
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(toRad(lat1)) * Math.cos(toRad(lat2)) * Math.sin(dLon / 2) * Math.sin(dLon / 2)
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a))
  return R * c
}

function toRad(deg: number): number {
  return deg * (Math.PI / 180)
}

// Interpolate between two points for animation
export function interpolatePosition(
  start: [number, number],
  end: [number, number],
  progress: number,
): [number, number] {
  return [start[0] + (end[0] - start[0]) * progress, start[1] + (end[1] - start[1]) * progress]
}

// Calculate total route distance
export function calculateRouteDistance(route: [number, number][]): number {
  let total = 0
  for (let i = 0; i < route.length - 1; i++) {
    total += haversineDistance(route[i][0], route[i][1], route[i + 1][0], route[i + 1][1])
  }
  return total
}
